/* reset */
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
div,
dl,
dt,
dd,
ul,
ol,
li,
p,
blockquote,
pre,
hr,
figure,
table,
caption,
th,
td,
form,
fieldset,
legend,
input,
button,
textarea,
menu {
  margin: 0;
  padding: 0;
}
header,
footer,
section,
article,
aside,
nav,
hgroup,
address,
figure,
figcaption,
menu,
details {
  display: block;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
caption,
th {
  text-align: left;
  font-weight: normal;
}
html,
body,
fieldset,
img,
iframe,
abbr {
  border: 0;
}
i,
cite,
var,
address,
dfn {
  font-style: normal;
}
li {
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
small {
  font-size: 100%;
}
sup,
sub {
  font-size: 83%;
}
pre,
code,
kbd,
samp {
  font-family: inherit;
}
q:before,
q:after {
  content: none;
}
textarea {
  overflow: auto;
  resize: none;
}
label,
summary {
  cursor: default;
}
a,
button {
  cursor: pointer;
}
body {
  background: #fff;
}
a,
a:hover {
  color: #333;
}

/*全局重写*/
body {
  font-size: 14PX!important;
  color: #222222;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.weui-cells {
  font-size: 14PX!important;
  margin-top: 0.133333rem!important;
}
.weui-cell {
  padding: 15PX!important;
}
.firstGroup .weui-cells {
  margin-top: 0;
}
.firstGroup .weui-cells:before {
  border-top: 0;
}
.vux-header {
  background-color: #fff!important;
  border-bottom: 1PX solid #f0f0f0!important;
}
.vux-header /deep/ .vux-header-title {
  color: #5f5f5f!important;
  font-size: 16PX!important;
}
.vux-header-left .left-arrow:before {
  border-color: #333!important;
}
.vux-popup-dialog {
  background-color: #f3f4f5;
}
.weui-form-preview__value {
  color: #000;
}
.vux-button-group > a.vux-button-group-current {
  background-color: #222!important;
  color: #fff!important;
}
.vux-button-group > a {
  color: #222!important;
}
.vux-button-group > a.vux-button-tab-item-first:after {
  border: 1PX solid #222!important;
}
.vux-button-group > a.vux-button-tab-item-last:after {
  border-right: 1PX solid #222!important;
  border-top: 1PX solid #222!important;
  border-bottom: 1PX solid #222!important;
}
.vux-button-group > a {
  line-height: 30PX;
}
.vux-number-selector {
  height: 18PX!important;
  font-size: 12PX!important;
  line-height: 12PX!important;
  border-radius: 9PX!important;
  box-sizing: border-box;
}
.vux-number-selector > svg {
  width: 12PX!important;
  height: 12PX!important;
  fill: #6b6a72!important;
}
.vux-number-round .vux-number-selector {
  width: 18PX!important;
}
.vux-number-round .vux-number-selector-sub,
.vux-number-round .vux-number-selector-plus {
  border-color: #a1a7ae!important;
}
.vux-number-round .vux-number-selector.vux-number-disabled {
  border-color: #ececec!important;
}
.vux-number-round .vux-number-selector.vux-number-disabled svg {
  fill: #ccc!important;
}
.weui-icon-circle,
.weui-icon-success-circle {
  font-size: 20PX!important;
}
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__icon,
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__icon > i,
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__label {
  color: #ffb000!important;
}
.weui-cells-top .weui-cells {
  margin-top: 0!important;
}
.weui-cells-top .weui-cells:before {
  border-top: 0;
}
.weui-icon-success-circle,
.weui-icon-success-no-circle {
  color: #FFB40C!important;
}
.vux-divider {
  color: #6b6a72!important;
  margin: 0 auto!important;
}
.vux-divider:after,
.vux-divider:before {
  width: 1.173333rem!important;
}
.vux-tab-bar-inner {
  border-radius: 2PX!important;
}
.weui-btn:after {
  border: none !important;
}
.vux-header .vux-header-title {
  color: #5f5f5f!important;
}

/*局部定义*/
.stickBtn {
  background: url(assets/images/Stick.png) no-repeat left top;
  background-size: 1.066667rem 1.066667rem;
  display: block;
  position: fixed;
  right: 0.266667rem;
  bottom: 1.6rem;
  width: 1.066667rem;
  height: 1.066667rem;
}
.location_b {
  background: url(assets/images/location_b.png) no-repeat left top;
  width: 0.4rem;
  height: 0.4rem;
  background-size: 0.4rem 0.4rem;
  display: block;
}
.location_w {
  background: url(assets/images/location_w.png) no-repeat left top;
  width: 0.4rem;
  height: 0.4rem;
  background-size: 0.4rem 0.4rem;
  display: inline-block;
  vertical-align: middle;
}
.disabled .location_b {
  background: url(assets/images/location_disabled.png) no-repeat left top;
  width: 0.4rem;
  height: 0.4rem;
  background-size: 0.4rem 0.4rem;
  display: block;
}
