/*只显示两行文字*/
.display2line {
  word-break: break-all;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
}

/*只显示一行文字*/
.display1line {
  word-break: break-all;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 1;
}

/*字体大小*/
.fs12 {
  font-size: 12PX;
}
.fs14 {
  font-size: 14PX;
}
.fs16 {
  font-size: 16PX;
}

/*0.5像素的线*/
.halfpx_buttom {
  background: linear-gradient(180deg, #ebebeb, #ebebeb, rgba(230, 230, 230, 0)) 0 100% no-repeat;
  background-size: 100% 1PX;
}
.halfpx_top {
  background: linear-gradient(180deg, #ebebeb, #ebebeb, rgba(230, 230, 230, 0)) 0 0 no-repeat;
  background-size: 100% 1PX;
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform;
  transition: all 300ms;
  height: 100%;
  top: 0;
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000;
          perspective: 1000;
}
.vux-pop-out-enter {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.vux-pop-out-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.vux-pop-in-enter {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.vux-pop-in-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}


